import { Heading } from '@chakra-ui/react';
import { Input, InputGroup, InputLeftElement } from '@chakra-ui/react';
import { EmailIcon, PhoneIcon } from '@chakra-ui/icons';
import { Textarea } from '@chakra-ui/react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './Contact.css';

function Contact() {
  return (
    <div>
      <div className="container-contact" id="c-contact" data-aos="fade-up" data-aos-duration="1000">
        <center>
          <Heading as="h3" size="lg" data-aos="zoom-in" data-aos-delay="100">
            Contact
          </Heading>
        </center>
        <div className="form-contact">
          <form>
            <h5>Name :</h5>
            <Input placeholder="Your name :" size="sm" marginBottom="10px" marginTop="5px" bgColor="white" color="#000" _placeholder={{ opacity: 0.4, color: 'inherit' }} />
            <h5>Telephone :</h5>
            <InputGroup>
              <InputLeftElement pointerEvents="none" children={<PhoneIcon color="gray.300" />} size="sm" />
              <Input type="tel" placeholder="Phone number" size="sm" marginBottom="10px" marginTop="5px" color="#000" _placeholder={{ opacity: 0.4, color: 'inherit' }} bgColor="white" />
            </InputGroup>
            <h5>Email :</h5>
            <InputGroup>
              <InputLeftElement pointerEvents="none" children={<EmailIcon color="gray.300" />} size="sm" />
              <Input type="tel" placeholder="Email" size="sm" marginBottom="10px" marginTop="5px" color="#000" _placeholder={{ opacity: 0.4, color: 'inherit' }} bgColor="white" />
            </InputGroup>
            <h5>Message :</h5>
            <Textarea placeholder="Write your message :" size="sm" marginBottom="10px" marginTop="5px" color="#000" _placeholder={{ opacity: 0.4, color: 'inherit' }} bgColor="white" />
            <br />
            <input class="btn btn-primary btn-sm" type="submit" value="Send Message" />
            <input class="btn btn-danger btn-sm" type="reset" value="Cancel" />
          </form>
        </div>
      </div>
    </div>
  );
}

export default Contact;
