import React, { useState } from 'react';
import './NavbarComponent.css';
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, NavbarText } from 'reactstrap';

function NavbarComponent(args) {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar {...args} className="navbarStyle navbar-dark bg-light fixed-top">
        <NavbarBrand href="/">
          Ikhwan{' '}
          <span className="text-danger">
            <b>Dev</b>
          </span>
        </NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="me-auto" navbar>
            <NavItem>
              <NavLink href="#c-home" active>
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#c-about">About</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="#c-skill">Skill</NavLink>
            </NavItem>
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle nav caret>
                Project
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem href="#c-project">Portofolio</DropdownItem>
                <DropdownItem disabled>Business</DropdownItem>
                <DropdownItem disabled>Other</DropdownItem>
              </DropdownMenu>
              <NavItem>
                <NavLink href="#c-contact">Contact</NavLink>
              </NavItem>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
}

export default NavbarComponent;
