import { Heading } from '@chakra-ui/react';
import 'bootstrap/dist/css/bootstrap.min.css';
import taskmanagementapp from '../../Img/taskmanagementapp.jpg';
import recipebookapp from '../../Img/recipebookapp.jpg';
import socialmediaapp from '../../Img/socialmediaapp.jpg';
import budgetingapp from '../../Img/budgetingapp.jpg';
import travelplaningapp from '../../Img/travelplaningapp.jpg';
import ecommerceapp from '../../Img/ecommerceapp.jpg';
import musicstreamingapp from '../../Img/musicstreamingapp.jpg';
import realestateapp from '../../Img/realestateapp.jpg';
import './Project.css';

function Project() {
  return (
    <div className="container-project" id="c-project" data-aos="fade-up" data-aos-duration="1000">
      <center>
        <Heading as="h3" size="lg" data-aos="zoom-in" className="h-project">
          My Project
        </Heading>
      </center>
      <div className="k-project">
        <div className="row row-cols-1 row-cols-md-4 g-4" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={taskmanagementapp} className="card-img-top" alt="Task Management App" />
              <div className="card-body">
                <h5 className="card-title">Task Management App</h5>
                <p className="card-text">This project could involve building a web or mobile application that allows users to create and manage tasks and projects.</p>
              </div>
            </div>
          </div>
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={recipebookapp} className="card-img-top" alt="Recipe Book App" />
              <div className="card-body">
                <h5 className="card-title">Recipe Book App</h5>
                <p className="card-text">This project could involve building a web or mobile application that allows users to search, save, and organize recipes.</p>
              </div>
            </div>
          </div>
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={socialmediaapp} className="card-img-top" alt="Social Media App" />
              <div className="card-body">
                <h5 className="card-title">Social Media App</h5>
                <p className="card-text">This project could involve building a web or mobile application that allows users to connect with friends and family, share updates and photos, and engage in online discussions.</p>
              </div>
            </div>
          </div>
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={budgetingapp} className="card-img-top" alt="Budgeting App" />
              <div className="card-body">
                <h5 className="card-title">Budgeting App</h5>
                <p className="card-text"> This project could involve building a web or mobile application that helps users track their spending and create a budget.</p>
              </div>
            </div>
          </div>
        </div>
        <div className="row row-cols-1 row-cols-md-4 g-4" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={travelplaningapp} className="card-img-top" alt="Travel Planing App" />
              <div className="card-body">
                <h5 className="card-title">Travel Planning App</h5>
                <p className="card-text">This project could involve building a web or mobile application that helps users plan and book trips, including flights, hotels, and activities.</p>
              </div>
            </div>
          </div>
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={ecommerceapp} className="card-img-top" alt="Ecommerce App" />
              <div className="card-body">
                <h5 className="card-title">E-commerce App</h5>
                <p className="card-text">Cooming soon...</p>
                {/* This project could involve building a web or mobile application that allows users to browse and purchase products online. */}
              </div>
            </div>
          </div>
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={musicstreamingapp} className="card-img-top" alt="Music Streaming App" />
              <div className="card-body">
                <h5 className="card-title">Music Streaming App</h5>
                <p className="card-text">Cooming soon...</p>
                {/* This project could involve building a web or mobile application that allows users to search and stream music from a variety of sources. */}
              </div>
            </div>
          </div>
          <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
            <div className="card h-100">
              <img src={realestateapp} className="card-img-top" alt="Real Estate App" />
              <div className="card-body">
                <h5 className="card-title">Real Estate App</h5>
                <p className="card-text">Cooming soon...</p>
                {/* This project could involve building a web or mobile application that allows users to search for and view real estate listings, and contact agents for more information. */}
              </div>
            </div>
          </div>
        </div>
      </div>
      <br />
    </div>
  );
}

export default Project;
