import { Image } from '@chakra-ui/react';
import NavbarComponent from '../../Components/NavbarComponent/NavbarComponent';
import 'bootstrap/dist/css/bootstrap.min.css';
import ToggleColorMode from '../../Components/ToggleColorMode/ToggleColorMode';
import AutoTyping2 from '../../Components/AutoTyping/AutoTyping';
import { Button, ButtonGroup } from '@chakra-ui/react';
import './Home.css';
import Profil from '../../Img/profil.jpg';

function Home() {
  return (
    <div className="containerHome">
      <span id="c-home"></span>
      <ToggleColorMode />
      <NavbarComponent />
      <center>
        <Image borderRadius="50%" boxSize="200px" src={Profil} alt="Ikhwanul Husna" marginTop="140px" boxShadow="5px 5px 10px rgba(60, 60, 60, 0.5)" />
        <p className="fs-3 text-center m-auto text-danger mt-2">Hi I'm Ikhwanul Husna</p>
        <AutoTyping2 />
        <br />
        <a href="#c-about">
          <Button marginTop="10px" colorScheme="telegram" variant="outline" boxShadow="3px 3px 10px rgba(60, 60, 60, 0.5)" marginBottom="110px">
            About Me
          </Button>
        </a>
      </center>
    </div>
  );
}

export default Home;
