import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
import { Heading } from '@chakra-ui/react';
import { Text } from '@chakra-ui/react';
import 'bootstrap/dist/css/bootstrap.min.css';
import bootstrap from 'bootstrap';
import { Progress } from 'reactstrap';
import './Skill.css';

function Skill() {
  AOS.init();
  return (
    <div className="container-skill" id="c-skill" data-aos="fade-up">
      <center>
        <Heading as="h3" size="lg" data-aos="zoom-in">
          My Skill
        </Heading>
      </center>
      <br />
      <Text fontSize="md" data-aos="fade-up">
        I have some expertise in programming, mastering several programming languages including HTML, CSS, Javascript, PHP, and several frameworks namely Bootstrap, Chakra UI, Tailwind, Jquery, and React JS. The following is a percentage
        of the programming languages or frameworks that I master.
      </Text>
      <br />
      <div className="myProgres">
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          HTML
        </Heading>
        <Progress className="progres-skill" animated value={85} data-aos="fade-left">
          85%
        </Progress>
        <br />
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          CSS
        </Heading>
        <Progress animated value={80} data-aos="fade-left">
          80%
        </Progress>
        <br />
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          Java Script
        </Heading>
        <Progress animated value={70} data-aos="fade-left">
          70%
        </Progress>
        <br />
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          Bootstrap
        </Heading>
        <Progress animated value={65} data-aos="fade-left">
          65%
        </Progress>
        <br />
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          Chakra UI
        </Heading>
        <Progress animated value={60} data-aos="fade-left">
          60%
        </Progress>
        <br />
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          Tailwind
        </Heading>
        <Progress animated value={55} data-aos="fade-left">
          55%
        </Progress>
        <br />
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          Jquery
        </Heading>
        <Progress animated value={52} data-aos="fade-left">
          52%
        </Progress>
        <br />
        <Heading className="h-skill" as="h2" size="md" data-aos="zoom-in">
          React JS
        </Heading>
        <Progress animated value={50} data-aos="fade-left">
          50%
        </Progress>
      </div>
    </div>
  );
}

export default Skill;
