import { Heading } from '@chakra-ui/react';
import { Text } from '@chakra-ui/react';
import './About.css';
import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
import 'bootstrap/dist/css/bootstrap.min.css';
import Bootstrapt from '../../Img/bootstrap.jpg';
import Chakraui from '../../Img/chakraui.jpg';
import Reactjs from '../../Img/reactjs.jpg';
import Nextjs from '../../Img/nextjs.jpg';

function About() {
  AOS.init();
  return (
    <div className="container-about" id="c-about" data-aos="fade-up" data-aos-duration="1000">
      <center>
        <Heading as="h3" size="lg" data-aos="zoom-in" data-aos-delay="100">
          About Me
        </Heading>
      </center>
      <br />
      <Text fontSize="md" data-aos="fade-up">
        I am a student majoring in informatics engineering who is very interested in the world of IT and wants to learn more about technology, especially in the field of programming. Currently I'm studying as a frontend web developer and
        trying to make several projects with the language or framework that I'm good at. Some of the programming languages that I learned include HTML, CSS, Javascript, PHP, Python, and Solidity. Then for the framework Bootstrap, Chakra UI,
        Jquery, React JS, and Vue JS. Here are some frameworks that I often use lately.
      </Text>
      <br />
      <div className="row row-cols-1 row-cols-md-4 g-4">
        <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
          <a href="https://getbootstrap.com/">
            <div className="card h-100">
              <img src={Bootstrapt} className="card-img-top" alt="Bootstrap" />
              <div className="card-body">
                <h5 className="card-title">Bootstrap</h5>
                <p className="card-text">Bootstrap is a front-end framework for building responsive, mobile-first websites and web applications.</p>
              </div>
            </div>
          </a>
        </div>
        <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
          <a href="https://chakra-ui.com/">
            <div className="card h-100">
              <img src={Chakraui} className="card-img-top" alt="Chakra UI" />
              <div className="card-body">
                <h5 className="card-title">Chakra UI</h5>
                <p className="card-text">Chakra UI is a library of reusable and accessible UI components for React. It's designed to be easy to use and customize, with a focus on simplicity and accessibility.</p>
              </div>
            </div>
          </a>
        </div>
        <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
          <a href="https://reactjs.org/">
            <div className="card h-100">
              <img src={Reactjs} className="card-img-top" alt="React JS" />
              <div className="card-body">
                <h5 className="card-title">React JS</h5>
                <p className="card-text">React is a JavaScript library for building user interfaces. It was developed by Facebook and is now widely used for building web applications.</p>
              </div>
            </div>
          </a>
        </div>
        <div className="col" data-aos="fade-up" data-aos-anchor-placement="center-bottom" data-aos-duration="1000">
          <a href="https://nextjs.org/">
            <div className="card h-100">
              <img src={Nextjs} className="card-img-top" alt="Next JS" />
              <div className="card-body">
                <h5 className="card-title">Next JS</h5>
                <p className="card-text">Next.js is a JavaScript framework for building server-rendered or statically exported React applications.</p>
              </div>
            </div>
          </a>
        </div>
      </div>
      <br />
    </div>
  );
}

export default About;
